import React from 'react';
import StorkLogoC from '../img/StorkLogoC.jpg'
const styles = {
    display: 'flex',
    justifyContent: "center"

};

const Img ={

    height:200,
    width:200
};

const Content ={
        padding: 10,
        margin: 10,
        backgroundColor: "",
        color: "",
        display: "inline-block",
        fontFamily: "monospace",
        fontSize: "20",
        textAlign: "center",
};

const Abt ={
    textAlign:"center"

};

const Content1={
    top: 0,
    zIndex: -1000,
    backgroundColor: '#FFFEF4',
    width: '100%'
};


const About = () => {
        return(
            <div className="about" style={styles}>
                <div>
                    <img style={Img} src={StorkLogoC}/>
                </div>
                <div>
                <h1 style={Abt}>About:</h1>
                <br/>
                <p style={Content}>Were parents, just like you. We recently had a baby and we quickly came to this realization:
                    The problem with baby stuff isnt that you dont know what to get.  You know exactly what you need, but there are so many choices. For everything. Hundreds of bottles. Just as many nipples. Bibs. Pacifiers. Countless car seats. Strollers that range in price from $50 to thousands. Diapers galore. Bouncers, swings, and chairs thatll make your head spin. Toys of all types. Cribs. Bassinets. Co-sleepers. And thats just the start! The options seem endless, so were here to help.
                    The Well-Stocked Stork is our brainchild. Were Willis and Darcy, a real-life couple with a newborn son who was born the summer of 2017, more than nine years after his older sister arrived on the scene. Boy oh boy, have things changed in a decade!
                    Weve combed through product reviews, ratings and old-fashioned recommendations from actual moms and dads to bring you the best products from a wide-array of retailers. The best part? Well tailor our suggestions to match your personality, budget and style.
                    When you use our recommendations, were confident that The Well-Stocked Stork will help you make the best selections to suit your family.
                    Heres to your next special delivery,

                    Willis and Darcy
                </p>
                </div>

            </div>
        )
};

export default About



